/*fuentes*/
@font-face {
  font-family: Mohan;
  src: url(./Archivos/Fuentes/Mohan.ttf);
  font-weight: 700;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  background-color: rgb(255, 255, 255);
  overflow-x: hidden;
  position: relative;

}
html {
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: normal;
}

p,
a,
li,
button,
ul {
  margin: 0;
  padding: 0;
  line-height: normal;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
/*Inicio*/
.banner{
  background-image: url(./Archivos/Imagenes/bannerfondo.jpg);
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  
}
.bannerrow{
 
  display: flex;
align-items: center;
justify-content: center;
}
.bannertext{
  display: inline-block;
  margin-left: 200px;
  
  
}
h1{
  font-family: 'mohan';
  color: #eaff82;
  font-size: 120px;
  text-align: center;
}
.apellido{
  margin-top: -50px;
}
ul li a{
  text-align: center;
  padding: 10px 60px;
  
  color: black;
  
  font-family: 'Montserrat', sans-serif;

}
ul li a:hover{color: #eaff82;}

ul li:hover{
  background-color: black;
 
}
ul li{
  border-radius: 35px;
  width: 120%;
  height: 50px;
  background-color: #eaff82;
  margin: 50px 0;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  
}





/*ABOUT*/
.aboutme{
  background-color: #eaff82;
  align-items: center;
  display: flex;
  flex-direction: row;
}
.aboutrow{
  display: flex;
align-items: center;
justify-content: center;
}
.abouttext{
  margin-left: 50px;
  width: 30%;
}
.fotoperfil{
  margin: 50px 0;
  width: 350px;
  height: 350px;
  border-radius:100%;
}
p{
  font-family: 'Lexend Exa', sans-serif;
  font-weight: 300;
  color: black;
}

/*proyectos*/

.projectrow{
  display: flex;
 
  align-items: center;
  justify-content: center;
}
.projects{
  margin: 50px 0;

}
@media(max-width:1207px) {
  .projectrow{
    margin-right: 50px;
  }
}
@media(max-width:1150px) {
  .projectrow{
    margin-left: -20px;
  }
  .projectcol {
    margin-left: -20px;
  }
}
.proj-imgbx {
  margin-left: 80px;
  color: white;
  width: 300px;
  height: 300px;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 30px;
  overflow: hidden;
  margin-bottom: 24px;
}
.proj-imgbx::before {
  content: "";
  background: linear-gradient(90.21deg, #1f1a1ab9 -5.91%, #000000d0
  111.58%);
  opacity: 0.85;
  position: absolute;
  width: 100%;
  height: 0;
  transition: 0.4s ease-in-out;
}
.proj-imgbx:hover::before {
  height: 100%;
}
.proj-txtx {
  font-family: 'Lexend Mega', sans-serif;
  font-weight: 100;
  position: absolute;
  text-align: center;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s ease-in-out;
  opacity: 0;
  width: 80%;
}
.proj-imgbx:hover .proj-txtx {
  top: 50%;
  opacity: 1;
}
.proj-txtx h4 {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1.1em;
}
.proj-txtx span {
  font-style: italic;
  font-weight: 400;
  font-size: 12px;
  letter-spacing: 0.8px;
 
 
}
/*NAVBAR*/
.nav{
  background-color: black;
  display: flex;
  flex-direction: row;
  height: 80px;
  text-align: center;
  align-items: center;
  justify-content: center;
}
.nav-item{
  margin: 0 100px;
}
.nav-link{
  font-size: 18px;
  color: white;
  font-family: 'Montserrat', sans-serif;
}
.nav-link:hover{

  
  color: #636060;
}
@media(max-width:1800px) {
  .nav-link{
    
    font-size: 14px;
  }
  .nav-item{
    margin: 0 80px;
  }
}
@media(max-width:1600px) {
  .nav-link{
    
    font-size: 14px;
  }
  .nav-item{
    margin: 0 60px;
  }
}
@media(max-width:1207px) {
  .nav-item{
    margin: 0 40px;
  }
}
/*GRID1*/
.grid-item {
  display: flex;
  align-items: center;
  justify-content: center;

  
  border-radius: 4px;
  transition: transform 0.3s ease-in-out;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.grid-item:hover {
  filter: opacity(0.9);
  transform: scale(1.04);
}

.grid-container {
  margin: 0 300px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-auto-rows: minmax(200px, auto);
  gap: 20px;
  padding: 20px;
  grid-auto-flow: dense;
}

